.header_content {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 32px;
}

.header_content__avatar {
    width: 187px;
    height: 187px;
    border-radius: 50%;
}

.header_content__name {
    font-family: Roboto;
    font-size: 64px;
    font-weight: 400;
    line-height: 75px;
    letter-spacing: 0em;
    text-align: left;
    color: white;
}

.header_content__position {
    font-family: Roboto;
    font-size: 32px;
    font-weight: 400;
    line-height: 38px;
    letter-spacing: 0em;
    text-align: left;
    color: white;
}

.header_content__load {
    cursor: pointer;
    background-color: rgb(234, 234, 225);
    border: 1px solid black;
    border-radius: 50%;
    padding: 5px 10px;
}

.header_content__loader {
    opacity: 0;
    position: absolute;
    z-index: -1;
}

.header_content_ava {
    position: relative;
}

.header_content__load {
    position: absolute;
    top: 150px;
    left: 140px;
}

@media (max-width: 376px) {
    .header_content {
        display: flex;
        flex-direction: column-reverse;
        gap: 16px;
        justify-content: center;
        align-items: center;
    }
    .header_content__name {
        font-family: Roboto;
        font-size: 36px;
        font-weight: 400;
        line-height: 42px;
        letter-spacing: 0em;
        text-align: center;
    }

    .header_content__position {
        font-family: Roboto;
        font-size: 20px;
        font-weight: 400;
        line-height: 23px;
        letter-spacing: 0em;
        text-align: center;
    }

    .header_content__text {
        display: flex;
        flex-direction: column;
        gap: 16px;
    }
}
