.container {
    padding-left: calc(50% - 720px);
    padding-right: calc(50% - 720px);
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media (max-width: 376px) {
    .container {
        padding-left: 0;
        padding-right: 0;
        width: 375px;
    }
}
